.nav {
  width: 100%;
  height: 40px;
  padding: 0 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid var(--light-gray);
  background-color: #E3EBEE;
  
}

.titleContainer {
  padding-left: 10px;
  /* border-left: 2px solid var(--primary-color-dark); */
}

.title {
  margin: 8px;
  font-size: 16px;
  font-weight: bold;
  color: black;
  font-family: Georgia, 'Times New Roman', Times, serif;
}

.info {
  font-size: 13px;
  color: var(--gray);
}

.button {
  width: 80px;
  height: 24px;
  /* padding: 14px 0px; */
  /* margin: 8px; */
  /* border: 2px solid black; */
  border-radius: 4px;
  color: black;
  align-items: center;
  vertical-align: auto;
  font-size: 14px;
  font-weight: bold;
  font-family: Georgia, 'Times New Roman', Times, serif;
  /* transition: all 150ms ease-in-out; */
}

.button:hover {
  background-color: rgba(128, 128, 128, 0.217);
  color: rgb(0, 0, 0);
}
