.container {
  display: grid;
  grid-template-columns: 1fr;
  /* 一列 */
  gap: 8px;
  /* 格子间隔 */
  height: 100%;
  /* 容器高度为视口高度 */
  align-content: center;
  /* 整体垂直居中 */
  justify-items: center;
}

.logo {
  height: 150px;
}

h2 {
  margin-top: 40px;
  margin-bottom: 70px;
  font-size: 70px;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.form {
  width: 80%;
  max-width: 480px;
  padding: 40px;
  display: grid;
  border: 1px solid var(--light-gray);
  border-radius: 12px;
  background-color: #E3EBEE;
  box-shadow: 0 4px 20px -5px var(--light-gray);
  grid-template-columns: 1fr;
  /* 一列 */
  gap: 8px;
  /* 格子间隔 */
  height: 100%;
  /* 容器高度为视口高度 */
  align-content: center;
  /* 整体垂直居中 */
  justify-items: center;
}

.input {
  margin: 10px 0;
  padding: 3px 10px;
  height: 40px;
  border: 1px solid var(--light-gray);
  border-radius: 4px;
  background: rgb(252, 252, 252);
  color: var(--black);
  font-size: 16px;
  outline: none;
  width: 80%;
  max-width: 300px;
}

.input::placeholder {
  color: var(--gray);
}

.error {
  padding-top: 6px;
  padding-left: 10px;
  color: rgb(213 122 122);
  font-size: 15px;
}

.buttons {
  margin-top: 150px;
  align-self: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.button {
  width: 160px;
  margin: 18px 15px;
  padding: 8px 0;
  border-radius: 25px;
  background-color: var(--primary-color-dark);
  color: rgb(0, 0, 0);
  font-size: 14px;
  font-weight: 500;
  transition: all 150ms ease-in-out;
}

.button:hover {
  background-color: var(--primary-color);
}
