:root {
  --primary-color: #F3E890;
  --primary-color-dark: #F3E890;
  --light-gray: #dddde3;
  --gray: #9f9f9f;
  --black: #333333;
  --primary-font: "Gill Sans", sans-serif;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
  color: var(--black);
}

h1 {
  font-weight: 700;
  font-size: 2.2rem;
  font-family: var(--primary-font);
  text-align: center;
  color: #2b0676;
}

button {
  border: none;
  background: none;
  outline: none;
  cursor: pointer;
}

textarea {
  flex-grow: 1; /* 让textarea占据所有剩余的空间 */
  resize: none; /* 禁用用户调整大小 */
  border: none; /* 可选：如果不想要边框 */
  margin: 0; /* 移除默认外边距 */
  padding: 8px; /* 添加内边距，让文字不会贴边显示 */
  font-size: 18px;
  /* 字体大小 */
  background-color: transparent;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
  /* MacOS下*/
  color: black;
}
